<template>
    <div id="timer">
      <span id="minutes">{{ seconds }}</span>
      <span id="middle">:</span>
      <span id="seconds">{{ milliseconds }}</span>
    </div>
</template>

<style>
    
</style>

<script>
  export default {
    data: () => {
      return {
        timer: null,
        totalTime: 2 * 1000,
      }
    },
    computed: {
        milliseconds: function() {
            return this.formatMS(this.totalTime % 1000);
        },
        seconds: function() {
            return Math.floor(this.totalTime / 1000);
        }
    },
    methods: {
      startTimer: function() {
        this.timer = setInterval(() => this.countdown(), 1);
      },
      stopTimer: function() {
        clearInterval(this.timer);
        this.timer = null;
      },
      resetTimer: function() {
        this.totalTime = (25 * 60);
        clearInterval(this.timer);
        this.timer = null;
      },
      countdown: function() {
        this.totalTime--;
        if (this.totalTime === 0) {
          alert('OVER');
        }
      },
      formatMS(ms) {
        if (ms > 99) {
          return ms;
        } else if (ms < 99 && ms > 9) {
          return `0${ms}`;
        } else {
          return `00${ms}`;
        }
      }
    }
  } 
</script>